




















































import { Component, Vue, Ref } from "vue-property-decorator";
import CallToAction from "../CallToAction.vue";
import FixedHeader from "../../components/header/FixedHeader.vue";
import Testimonials from "../Testimonials.vue";
import Api from "../../Api";

@Component({
  components: {
    CallToAction,
    FixedHeader,
    Testimonials,
  },
})
export default class OurTeam extends Vue {
  programName = this.$route.params.programName.split("-").join(" ");
  program = {
    Description: "",
    Searching_for: "",
    Through_Training: "",
    Video: "",
  };

  async created() {
    await this.fetchProgram();
  }

  async fetchProgram() {
    let filterBy = "{id}";
    let filter = this.$route.params.id;
    if (filter === undefined) {
      filterBy = "[id][_eq]={id}";
      filter = "[Name][_eq]=" + this.programName;
    }
    await fetch(Api.Programs.GetProgram.replace(filterBy, filter))
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          response;
        }
      })
      .then((data) => {
        this.program = data.data[0];
        if (this.program.Video != null)
          this.program.Video = Api.LocalhostImages + this.program.Video;
      })
      .catch((error) => {
        console.log(error);
      });
  }
}
